<template>
    <div>
        <h1> .</h1>

        <div class="card text-white bg-primary mb-3" style="max-width: 20rem;" v-if="user.name">
            <div class="card-header">{{ user.name }} .</div>
            <div class="card-body">
                <h4 class="card-title">     : {{user.name}}</h4>
                <h4 class="card-title"> : {{user.tel}}</h4>
                <h4 class="card-title">     : {{user.addr}}</h4>
                <h4 class="card-title">   : {{user.email}}</h4>
                <h4 class="card-title">     : {{user.account}}</h4>
                <h4 class="card-title">     : {{user.money}}</h4>
                <br>
                <h3 class="card-text" v-if="Store"> : {{Store.company}}</h3>
            </div>
        </div>

        <div v-if="Store.seller == 1">
            <router-link to="/CreateStore" class="nav-link">
                <button type="button" class="btn btn-primary"> </button>
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: "MyPage",
        data () {
            return {
                user : {},
                Store : {}
            }
        },
        methods:{
            onSubmit() {
                this.$store.dispatch('GetProfile')
                    .then( response => {
                        //alert('  : '+JSON.stringify(response.data.user));
                        console.log(' ');
                        this.user = response.data.user;
                        //console.log('this.name : '+JSON.stringify(this.user));
                    })
                    .catch( err => {
                        console.log(' ' + err);
                        alert(err);
                        this.$router.replace({path : '/Login'});
                    })
            }
        },


        created() {
            this.$store.dispatch('GetProfile')
                .then( response => {
                    //alert('  : '+JSON.stringify(response.data.user));
                    console.log(' ');
                    this.user = response.data.user;
                    //console.log('response : '+JSON.stringify(response));
                    if( 0 == response.data.user.indi) {
                        let UserNumber = {
                            number : response.data.user.number
                        };
                        return this.$store.dispatch('FoundEnt', UserNumber);
                    } else {
                        console.log('   : ');
                        alert('  ');
                        return ' ';
                        //this.$store.dispatch('LOGOUT');
                        //this.$router.replace({path : '/Login'});
                    }
                })
                .then( res => {
                    this.Store = res.data.store
                    //console.log('res : '+JSON.stringify(res));
                    if (res.data.store.seller === 1) {
                        this.newStore.seller = res.data.store.company;
                        this.newStore.number = res.data.store.number;

                        console.log('  ');
                    } else {
                        console.log('  ');
                        alert('  ');
                        //this.$store.dispatch('LOGOUT');
                        //this.$router.replace({path : '/Login'});
                    }
                })
                .catch( err => {
                    //console.log(' ' + JSON.stringify(err));
                    //alert(err);
                    if(err == ' ') {
                        console.log('  err 1');
                    }
                    else if(err.response.status === 401) {
                        console.log(' ' + JSON.stringify(err));
                        this.$store.dispatch('LOGOUT');
                        this.$router.replace({path : '/Login'});
                    } else {
                        console.log('  err 2');
                    }
                })
        }

    }
</script>

