<template>
    <div class="row">
        <div class="col-md-3"></div>
        <div class="col-md-6">
            <h2>{{newStore.seller}}</h2>
            <h2> </h2>
            <h2>   </h2>
            <div class="form-group row">
                <label for="name" class="col-sm-2 col-form-label"></label>
                <div class="col-sm-10">
                    <input type="text" id="name" v-model="newStore.name" class="form-control" >
                </div>
                <br><br><br>

                <label for="price" class="col-sm-2 col-form-label"></label>
                <div class="col-sm-10">
                    <input type="text" id="price" v-model="newStore.price" class="form-control" >
                </div>
                <br><br><br>

                <label for="quantity" class="col-sm-2 col-form-label"></label>
                <div class="col-sm-10">
                    <input type="text" id="quantity" v-model="newStore.quantity" class="form-control">
                </div>
                <br><br><br>

                <label for="exampleSelect" class="col-sm-2 col-form-label"></label>
                <select class="form-control col-sm-10" id="exampleSelect" v-model="newStore.category">
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value="//DVD">//DVD</option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                </select>
                <br><br><br>

                <label for="description" class="col-sm-2 col-form-label"></label>
                <div class="col-sm-10">
                    <input type="text" id="description" v-model="newStore.description" class="form-control">
                </div>
            </div>
            <button @click="newStoreSubmit" type="button" class="btn btn-primary"></button>
            <div class="col-md-3"></div>
        </div>
    </div>
</template>

<script>

    export default {
        name: "CreateStore",
        data(){
            return {
                newStore : {
                    name: '',
                    price: '',
                    quantity: '',
                    category: '',
                    description: '',
                    seller: '',
                    number : '',
                }
            }
        },
        methods : {
            newStoreSubmit : function () {
                this.$store.dispatch('NewProduct', this.newStore)
                    .then( response => {
                        if(response.data.success === true) {
                            alert('  ');
                            console.log('  ');
                            this.$router.replace({ path : '/AllProduct' });
                        } else {
                            alert('   1');
                            console.log('   1');
                        }
                    }). catch((err) => {
                        alert('   2');
                        console.log('   2');
                    });
            }
        },

        created() {
            this.$store.dispatch('GetProfile')
                .then( response => {
                    //alert('  : '+JSON.stringify(response.data.user));
                    console.log(' ');
                    //console.log('response : '+JSON.stringify(response));
                    if( 0 == response.data.user.indi) {
                        let UserNumber = {
                            number : response.data.user.number
                        };
                        return this.$store.dispatch('FoundEnt', UserNumber);
                    } else {
                        console.log('  ');
                        alert('  ');
                        this.$store.dispatch('LOGOUT');
                        this.$router.replace({path : '/Login'});
                    }
                })
                .then( res => {
                    //console.log('res : '+JSON.stringify(res));
                    if (res.data.store.seller === 1) {
                        this.newStore.seller = res.data.store.company;
                        this.newStore.number = res.data.store.number;
                        console.log('  ');
                    } else {
                        console.log('  ');
                        alert('  ');
                        this.$store.dispatch('LOGOUT');
                        this.$router.replace({path : '/Login'});
                    }
                })
                .catch( err => {
                    console.log(' ' + err);
                    //alert(err);
                    this.$store.dispatch('LOGOUT');
                    this.$router.replace({path : '/Login'});
                })
        }
    }
</script>

